// NOVELTY ====================================
	//common
		.novelty {
			position: relative;
			.clearfix();
			padding-top: 142/16em;
			padding-bottom: 190/16em;
			padding-left: 80/16em;
			background-image: url("../assets/img/bg_lightgrey.jpg");
			background-size: cover;
			color: @darkgrey;
			&:before {
				position: absolute;
				top: 0;
				left: 50%;
				display: block;
				width: 2px;
				height: 14%;
				background-color: @darkgrey;
				opacity: 0.7;
			}
		}
		.novelty__stick {
			position: absolute;
			bottom: 7%;
			left: 50%;
			display: block;
			width: 2px;
			height: 28%;
			background-color: @darkgrey;
			opacity: 0.7;
			z-index: 0;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: -2.5px;
				display: block;
				width: 7px;
				height: 7px;
				transform: rotate(45deg);
				background-color: @darkgrey;
			}
		}
	// slider
		.novelty__slider-wrapp {
			.clearfix();
		}

		.novelty__desc-slide
		{
			position: relative;
			height: 300/16em;
		}

		.novelty__desc {
			position: relative;
			float: left;
			width: 23.7%;
			height: 370/16em;
			overflow: hidden;
		}
		.novelty__caption {
			.font_bold_cn();
			font-size: 53/16em;
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 2px;
				margin-top: 5/53em;
				margin-bottom: 10/53em;
				background-color: @darkgrey;
			}
		}
		.novelty__text {
			position: relative;
			max-height: 84%;
			padding-right: 12%;
			text-transform: none;
			.font_light();
			font-size: 12px;
			@media screen and (min-width: 1365px) { font-size: 12/16em; }
			line-height: 1.1;

			text-align: justify;
			text-align-last: left;
			overflow: hidden;
			text-overflow: ellipsis;
		}
		.novelty__button {
			position: absolute;
			bottom: 0;
			right: 10/25em;
			padding: 6/25em 11/25em;
			border: 1px solid @white;
			font-size: 25/16em;
			.font_reg_cn();
			background-color: @orange;
			color: @white;
		}
		.novelty__slider {
			position: relative;
			float: left;
			width: 76.3%;
			height: 370/16em;		
			z-index: 1;	
		}

		.novelty__slide {
			position: relative;
			display: inline-block;
			// margin-right: 43/16em;
			padding-right: 43/16em;
			// z-index: 100;
			&:after {
				content: '';
				z-index: -1;
				position: absolute;
				right: -50/16em;
				top: 58/16em;
				display: block;
				width: 100/16em;
				height: 2px;
				background-color: @darkgrey;
			}

			.slide__signature {
				position: absolute;
				right: 43/33em;
				bottom: 35/33em;
				font-size: 33/16em;
				width: 440/33em;
				padding: 11/33em 53/33em 11/33em 30/33em;
				background-color: rgba(255, 255, 255, 0.8)
			}
		}

		.slick-slide
		{
			outline: none;
		}

		.novelty__slider > .slick-list
		{
			padding:0 ~"calc(100% - 52em)" 0 0 !important;
		}

		.slide__img {
			z-index: 1;
			width: 790/16em;
		}
	// navigation
		.novelty__nav {
			position: relative;
			float: right;
			display: inline-block;
			margin-top: 27/16em;
			margin-right: 110/16em;

			// *
			// {
			// 	display: inline-block;
			// 	vertical-align: middle;
			// }

			.slider-dot > li {
				vertical-align: middle;
				cursor: pointer;
				margin-right: 30/16em;

				&:first-child {
					margin-left: 30/16em;
				}
			}

			&:before {
				content: '';
				position: absolute;
				top: 12/16em;
				left: 29/16em;
				display: block;
				width: 80%;
				height: 1px;
				background-color: @darkgrey;
			}

			.right-arrow:before {
				border-color: #ccc #ccc transparent transparent;
			}
		}

		.novelty__dots
		{
			position: relative;
			top: -11/16em;
			display: inline-block;
		}
// END NOVELTY ================================