// NEWS =======================================
	// common
		.news
		{
			position: relative;
			// padding-bottom: 103/16em;
			text-align: center;
			background-image: url("../assets/img/bg_lightgrey.jpg");
			background-size: cover;
		}

		.news__stick
		{
			.products__stick;
			top: 0/16em;
			height: 120/16em;
			margin-bottom: 20/16em;
		}

		.news__caption
		{
			.caption__default;
			margin-bottom: 42/39em;
		}
	// slider
		.news__slider
		{
			position: relative;
			width: 98%;
			max-width: 1035/16em;
			.center();
			.clearfix();
		}

		.news__slide
		{
			position: relative;
			width: 500/16em;
			margin-left: 1em;
			margin-right: 1em;
			padding-bottom: 160/16em;



			img
			{
				width: 100%;
			}
		}

		.news__signature
		{
			position: absolute;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 370/26em;
			padding: 28/25em 65/25em 18/25em;
			font-size: 25/16em;
			background-color: @darkgrey;
			color: @white;

			&:before
			{
				content: '';
				position: absolute;
				left: 50%;
				top: -22/25em;
				display: block;
				width: 1px;
				height: 45/25em;
				background-color: @white;

			}

			*
			{
				display: block;
			}
		}

		.news__name
		{
			margin-bottom: 0.3em;
		}

		.news__about
		{
			font-size: 19/25em;
			@media screen and (max-width: 977px) { font-size: 14px; }
			text-transform: none;
		}
	// arrows
		.news__left-arrow
		{
			.left-arrow;
			position: absolute;
			left: 110/16em;
			top: 352/16em;
		}

		.news__right-arrow
		{
			.right-arrow;
			position: absolute;
			right: 110/16em;
			top: 352/16em;

			&:before
			{
				border-color: #ccc #ccc transparent transparent;
			}
		}


// END NEWS ===================================
