// FEEDBACK ===================================
	// common
		.feedback
		{
			padding-top: 145/16em;
			padding-bottom: 102/16em;
			text-transform: none;
			color: @darkgrey;
			text-align: center;
			background-image: url("../assets/img/bg_feedback.jpg");
			background-size: cover;
		}
		
		.feedback__wrapper
		{
			width: 98%;
			max-width: 760/16em;
			.center();
		}
		.feedback__caption
		{
			.caption__default;
			text-transform: uppercase;
			margin-bottom: 33/39em;
		}
	// slider
		.feedback__slider
		{
			position: relative;
			.clearfix();
			width: 1050/16em;
			padding-left: 240/16em;
			text-align: left;
		}

		.feedback__slider-wrap
		{
			position: relative;
			overflow: hidden !important;
			padding: 0 0 100/16em 33/16em;
			float: left;
		}

		.feedback__pics
		{
			width: 585/16em;
			z-index: 1000;

			.slick-list
			{
				overflow: visible;
			}

		}
		
		.feedback__pic--active
		{
			transform-origin: top;
			transform: scale(1.39);
			z-index: 10;
			box-shadow: 5/16em 5/16em 13/16em -5/16em rgba(0,0,0,0.75);

			.feedback__pic-sign
			{
				opacity: 0.7;
			}
		}

		.feedback__pic
		{
			position: relative;
			transition: all 300ms;

			&:not(.slick-current)
			{
				cursor: pointer;
			}

			&:not(:last-child)
			{
				margin-right: 6/16em;
			}

			img
			{
				position: relative;
				width: 100%;
			}

			// active slide
			&.slick-current
			{
				.feedback__pic--active;
			}
		}

		.feedback__pic-sign
		{
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 29/15em;
			line-height: 29/15em;
			text-align: center;
			font-size: 15/16em;
			.font_reg_cn();
			background-color: #fff;
			opacity: 0;
			transition: all 300ms;
		}

		.feedback__nav
		{
			position: relative;
			float: right;
			width: 190/16em;
			height: 195/16em;

			&:before
			{
				content: '';
				position: absolute;
				left: 64/16em;
				top: 169.5/16em;
				display: block;
				width: 92/16em;
				height: 1px;
				background-color: @darkgrey;
			}
		}

		.feedback__left-arrow
		{
			position: absolute;
			left: 33/16em;
			bottom: 12/16em;
		}

		.feedback__right-arrow
		{
			position: absolute;
			right: 4/16em;
			bottom: 12/16em;

			&:before
			{
				border-color: #F3F3F5 #F3F3F5 transparent transparent;
			}
		}
	// reviews slider
		.feedback__reviews
		{
			position: relative;
			top: -60/16em;
			width: 98%;
			max-width: 760/16em;
			.center();
		}

		.feedback__reviews-wrap
		{
			padding-bottom: 1px; //      .feedback__text      
		}

		.feedback__text
		{
			padding: 75/20em 40/20em 60/20em;
			border: 1px solid @darkgrey;
			.font_light();
			text-align: left;
			font-size: 20/16em;
			@media screen and (max-width: 977px) { font-size: 14px; }
			line-height: 1.15;
		}
// END FEEDBACK ===============================