// FIRST SCREEN ===============================
	// logo + contacts
		.first
		{
			position: relative;
			color: @white;
		}

		.first__bg-slider
		{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			div
			{
				height: 100%;
				background-size: cover;
			}
		}

		.first__wrap 
		{
			position: relative;
			padding-top: 21/16em;
			padding-left: 100/16em;
			padding-right: 60/16em;
			padding-bottom: 70/16em;
			z-index: 1000;
		}

		.first__contacts-wrap 
		{
			.clearfix();
			display: inline-block;
			width: 50%;
		}

		.first__logo 
		{
			float: left;
			width: 7.8em;
			padding-right: 28/16em;
		}

		.first__contacts 
		{
			float: left;
			margin-top: 5/16em;
		}

		.contacts__address 
		{
			font-size: 21/16em;
			display: block;
			text-transform: none;
			&:after {
				content: '';
				display: block;
				width: 100%;
				height: 1px;
				margin-top: 2/21em;
				margin-bottom: 6/21em;
				background-color: @white;
			}
		}

		.contacts__phone 
		{
			display: inline-block;
			font-size: 27/16em;
						
			&:before
			{
				display: inline-block;
				position: relative;
				bottom: 2/29em;
				margin-right: 7/29em;
				font-size: 0.8em;
			}
		}

		.contacts__button 
		{
			position: relative;
			float: right;
			font-size: 20/16em;
			border: 1px solid @white;
			padding: 3/20em 12/20em;
			background: @orange;
		}

		// .contacts__email
		// {
		// 	float: right;
		// 	font-size: 27/16em;
		// 	text-transform: none;
		// }
	// user basket and cabinet
		.user 
		{
			margin-top: 17/16em;
			float: right;
		}

		.user__basket 
		{
			font-size: 23/16em;
			display: inline-block;
			margin-bottom: 20/23em;
			padding: 13/23em 20/23em;
			border: 1px solid @white;
			background-color: @darkgrey;
			opacity: 0.7;

			&:before 
			{
				font-size: 1em;
				margin-right: 10/23em;
			}
		}

		.user__cabinet 
		{
			padding-top: 4/16em;
			padding-left: 12/16em;
			padding-right: 12/16em;
			padding-bottom: 16/16em;
			border: 1px solid @white;
			background-color: @darkgrey;
			opacity: 0.7;
		}

		.cabinet__caption 
		{
			display: block;
			text-align: center;
			font-size: 20/16em;
			margin-bottom: 14/20em;

			&:before 
			{
				position: relative;
				top: 0.2em;
				margin-right: 14/30em;
				font-size: 1.5em;
			}
		}
		.cabinet__login,
		.cabinet__reg 
		{
			display: block;
			padding: 4px 5px;
			border: 1px solid @white;
			font-size: 17/16em;
			text-align: center;
		}

		.cabinet__login 
		{
			margin-bottom: 10/17em;
		}
	// navigation
		.first__nav 
		{
			margin-top: 29/16em;
			margin-bottom: 70/16em;
		}

		.nav__item 
		{
			display: inline-block;
			font-size: 20/16em;
			margin-right: 12/20em;
		}

		.nav__item--active 
		{
			color: #000;
		}
	// slider
		.first__bricks 
		{
			.font_reg_cn();
			position: relative;
			top: -60/16em;
			margin-bottom: -40/16em;

			.slick-list
			{
				// padding-bottom: 70/16em; //   ,     overflow:hidden
			} //     first__wrap

		}

		.first__brick:not(:first-child)
		{
			opacity: 0;
			position: absolute;
			left: 0;
			top: 0;
		}

		.bricks__desc 
		{
			// float: left;
			position: relative;
			display: inline-block;
			margin-right: 145/16em;
		}

		.brick__category 
		{
			.clearfix();
		}

		.category__name 
		{
			float: left;
			padding: 2/19em 12/19em 0;
			color: #000;
			background-color: #fff;
			opacity: 0.7;
			font-size: 19/16em;
		}

		.category__showall 
		{
			float: left;
			padding: 2/19em 8/19em 0;
			font-size: 19/16em;
			background-color: @darkgrey;
			opacity: 0.7;
		}

		.bricks__name 
		{
			min-width: 330/91em;
			font-size: 91/16em;
			.font_bold_cn();
			margin-bottom: 3/91em;
		}

		.brick__features-caption 
		{
			font-size: 30/16em;
			margin-bottom: 4/30em;
			color: wheat;
		}

		.bricks__features 
		{
			padding-left: 24/16em;
			.font_light_cn();
		}

		.bricks__feature 
		{
			font-size: 22.5/16em;
			text-transform: none;
			list-style: square outside;

			&:not(:last-of-type) 
			{
				margin-bottom: 6/25em;
			}
			
		}

		.bricks__button 
		{
			position: absolute;
			right: 0;
			bottom: 0;
			padding: 7/25em 11/25em;
			border: 1px solid @white;
			font-size: 25/16em;
			background-color: @orange;
		}

		.bricks__pic 
		{
			position: relative;
			display: inline-block;
			padding-top: 95/16em;
			width: 100px;

			img 
			{
				position: absolute;
				z-index: 1; //  bricks__border
				left: 25/16em;
				bottom: 7/16em;
				display: block;
				width: 463/16em;
			}
		}

		@keyframes borderScale
		{
			0%
			{
				transform: scale(0.9);
				opacity: 1;
			}

			100%
			{
				transform: scale(1.6);
				opacity: 0;
			}
		}

		.bricks__border
		{
			position: relative;
			display: block;
			width: 365/16em;
			height: 215/16em;
			border: 1px solid @white;

			&:before,
			&:after
			{
				content: '';
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 100%;
				border: 1px solid #fff;
			}

			&:before
			{
				animation: borderScale 3s ease-out infinite;
			}
			
			&:after
			{
				animation: borderScale 3s 1.5s ease-out infinite;
			}

		}
	// fixed navigation
		.fixed-nav
		{
			position: relative;
			z-index: 1001;
		}

		.fixed-nav--fixed
		{
			position: fixed;
			left: 0;
			top: 0;
			width: 100%;
		}

		.fixed-nav__contacts
		{
			display: none;
			padding-top: 10/29em;
			padding-bottom: 2/29em;
			padding-left: 100/29em;
			padding-right: 60/29em;
			.clearfix();
			font-size: 26/16em;
			background-color: rgba(255, 255, 255, 0.9);
			color: @darkgrey;
		}

		.fixed-nav__phone
		{
			float: left;
			border-bottom: 1px dashed @darkgrey;
			margin-right: 6%;

			&:before
			{
				position: relative;
				bottom: 2/29em;
				margin-right: 7/29em;
				font-size: 0.8em;
			}
		}

		.fixed-nav__hours
		{
			float: left;
			margin-right: 6%;

			&:before
			{
				position: relative;
				bottom: 2/29em;
				margin-right: 7/29em;
				font-size: 0.8em;
			}
		}

		.fixed-nav__address
		{			
			float: left;
			text-transform: none;
			border-bottom: 1px dashed @darkgrey;

			&:before
			{
				position: relative;
				bottom: 2/29em;
				margin-right: 7/29em;
				font-size: 0.9em;
			}
		}

		.fixed-nav__button
		{
			float: right;
			margin-right: 2.6%;
			padding: 2/22em 10/22em;
			font-size: 22/29em;
			text-transform: uppercase;
			border: 1px solid @white;
			background: @orange;
			color: @white;
		}

		.fixed__user
		{
			position: relative;
			bottom: 5/29em;
			float: right;
		}

		.fixed-nav__basket,
		.fixed-nav__cabinet
		{
			position: relative;
			display: inline-block;
			width: 37/29em;
			height: 37/29em;
			border: 1px solid @darkgrey;
			text-align: center;
			padding-top: 3/29em;
			opacity: 0.9;
		}

		.fixed-nav__basket
		{
			margin-right: 10/29em;
		}
	
		.first__filter 
		{
			background-color: @darkgrey;
			opacity: 0.9;
		}

		.filter__wrap 
		{
			padding-left: 100/16em;
			padding-right: 60/16em;
		}

		.filter__list 
		{
			width: 100%;
		}

		.filter__item 
		{
			width: 1%;
			display: table-cell;
			height: 1.8em;
			font-size: 26/16em;
			vertical-align: middle;
			text-align: center;
			opacity: 0.5;
		}

		.filter__item--active 
		{
			opacity: 1;
		}

	// icons scale on hover
		.contacts__phone,
		// .user__basket,
		// .fixed-nav__phone,
		// .fixed-nav__address,
		// .fixed-nav__basket,
		// .fixed-nav__cabinet
		{
			&:before
			{
				display: inline-block;
				transition: all 200ms;
			}

			&:hover:before
			{
				transform: scale(1.1);
			}
		}
// END FIRST SCREEN ===========================

