		.slider__wrapper
		{
			width: 80%;
			.center();
		}
	// thumbnails
		.slider__thumbs-wrap
		{
			overflow: hidden;
			padding-top: 10/16em;
			margin-top: -10/16em;
		}

		.slider__thumbs
		{
			height: 55/16em;
			padding-top: 5/16em;
			margin-bottom: 2.7%;
			.center();
			border-top: 3px solid @darkgrey;
			white-space: nowrap;

			.slick-list
			{
				overflow: visible;
			}
		}

		.thumbs__img
		{
			width: 100/16em;
			cursor: pointer;
			transition: all 100ms;

			&:not(:last-of-type)
			{
				margin-right: 2px;
			}

			&:hover,
			&.slick-current
			{
				transform: scale(1.3);
				box-shadow: 0px 0px 10/16em 2/16em rgba(0, 0, 0, 0.75);
			}
		}
