			// selects
				.select-hidden 
				{
				  display: none;
				  visibility: hidden;
				  padding-right: 10/20em;
				}

				.select 
				{
				  cursor: pointer;
				  display: inline-block;
				  position: relative;
				  // font-size: 1em;
				  // color: $select-color;
				  width: 180/20em;
				  border: 1px solid @darkgrey;
				  height: 40/20em;

				  &:not(:last-of-type)
				  {
				  	margin-right: 20px;
				  }


				}

				.select-styled 
				{
				  position: absolute; 
				  top: 0;
				  right: 0;
				  bottom: 0;
				  left: 0;
				  border: 1px solid @darkgrey;
				  background-color: #d4d3d9;
				  padding: 8/20em 15/20em 8/20em 50/20em;
				  transition: all 200ms;
				  .noselection();
				  .link-hide-select();
				  z-index: 101;
				  
				  &:before 
				  {
				    content: '';
				    position: absolute;
				    left: 0;
				    top: 0;
				    width: 40/20em;
				    height: 100%;
				    background-color: @darkgrey;
				  }
				  
				  &:after 
				  {
				    content:"";
				    width: 0;
				    height: 0;
				    border: 7/20em solid transparent;
				    border-color: #fff transparent transparent transparent;
				    position: absolute;
				    top: 16/20em;
				    left: 12/20em;
				    // right: 10px;
				  }
				  
				  &:hover 
				  {
				  	// background-color: #d4d3d9;
				    // background-color: darken(rgba(0, 0, 0, 0), 5);
				  }
				  
				  &:active, &.active 
				  {
				    // background-color: darken(rgba(0, 0, 0, 0), 5);
				    
				    &:after 
				    {
				      top: 9/20em;
				      border-color: transparent transparent #fff transparent;
				    }
				  }
				}

				.select-options 
				{
				  display: none; 
				  position: absolute;
				  top: 103%;
				  right: 0;
				  left: 0;
				  z-index: 100;
				  margin: 0;
				  padding: 0;
				  list-style: none;
				  background-color: #d4d3d9;
				  .link-hide-select();
				  box-shadow: 3/16em 3/16em 20/16em 0/16em rgba(0,0,0,0.4);
				  border-radius: 3/16em;

				  li 
				  {
				    margin: 0;
				    padding: 12/20em 0;
				    text-indent: 15/20em;
				    border-top: 1px solid darken(#d4d3d9, 10);
				    transition: all 50ms ease-in;
				    
				    &:hover 
				    {
				      background: darken(#d4d3d9, 10);
				    }    
				    &[rel="hide"] 
				    {
				      display: none;
				    }
				  }
				}

			// end of selects
		}


	// slide cards
		.slider__cards
		{
			z-index: 10;
			padding-left: 21.5%;
			padding-right: 21.5%;
			padding-left: 19%;
			padding-right: 19%;
			overflow: hidden;
			padding-top: 40/16em;
			padding-bottom: 40/16em;

			.slick-list
			{
				overflow: visible;
			}
		}

		.slider__item
		{
			position: relative;
			padding:  1.2em 1.2em 1.2em 1.2em;
			// width: 51%;
			// width: 55.5%;
			// width: 750/16em;
			z-index: 10;
			.center();
			// margin-top: -10/16em;
			// padding-top: 10/16em;
			// padding-left: 30/16em;
			// padding-right: 30/16em;			
			// padding-bottom: 30/16em;
			margin-left: 35/16em;
			margin-right: 35/16em;
			cursor: pointer;
			transition: all 100ms;

			transform: translate3d(0, 0, 0); //     

			&.slick-current
			{
				.prices__discount
				{
					background-color: #fff;
				}
				&:hover
				{
					// padding-top: 0.2em;
					transform: scale(1.04);
					box-shadow: 0px 0px 40px 0px rgba(0,0,0,0.6);
					
					//   
					.slider-item__wall-overlay
					{
						opacity: 1;
					}

					&:hover
					{
						~.products__left-arrow,
						~.products__right-arrow			
						{
							opacity: 0;
						}
					}
				}

				.slider-item__overlay
				{
					background-color: transparent;
				}
			}
		}

		.slider-item__overlay
		{
			position: absolute;
			top: 0;
			left: 0;
			z-index: 10;
			width: 100%;
			height: 100%;
			padding: 1.2em;
			transition: all 200ms;
			transition-delay: 200ms;
			background-color: rgba(47, 57, 65, 0.15);
			background-clip: content-box;
		}

		.desc__wrapper
		{
			.clearfix();
			width: 92%;
			.center();

			*
			{
				display: block;
			}
		}

		.desc__params
		{
			float: left;
		}

		.params__type
		{
			margin-top: 0.7em;
			margin-bottom: 0.2em;
			font-size: 30/16em;
			color: @white;
		}

		.params__name
		{
			margin-bottom: 0.1em;
			font-size: 92/16em;
			.font_reg_cn();
		}

		.params__list
		{
			margin-bottom: 5em;
			text-transform: none;

			li
			{
				font-size: 25/16em;

				&:before
				{
					margin-right: 14/25em;
					font-size: 0.9em;
				}

				&:not(:last-of-type)
				{
					margin-bottom: 0.6em;
				}
			}
		}

		.desc__prices
		{
			float: right;
			text-align: right;
			text-transform: none;
		}

		.prices__cost
		{
			margin-top: 0.4em;
			margin-bottom: 0.3em;
			font-size: 33/16em;
			text-decoration: line-through;
		}

		.prices__discount
		{
			margin-bottom: 0.1em;
			padding: 0.14em 0.15em;
			font-size: 70/16em;
			transition: all 300ms;
		}

		.prices__opt
		{
			margin-bottom: 0.4em;
			font-size: 40/16em;
			.font_reg_cn();
		}

		.prices__button
		{
			display: inline-block;
			padding: 0.25em 0.45em;
			font-size: 25/16em;
			.font_reg_cn();
			text-transform: uppercase;
			border: 1px solid @white;
			background-color: @orange;
			color: #fff;
		}
	// slide images
		.slider-item__brick
		{
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			top: 40.6%;
			width: 350/16em;
			height: 120/16em;
			border: 1px solid @darkgrey;

			img
			{
				position: relative;
				bottom: 93/16em;
				left: 50/16em;
				// width: 75%;
				width: 260/16em;
			}

		}

		.slider-item__wall
		{
			position: relative;
			height: 312/16em;
			background-image: url("../assets/img/products_wall.jpg");
			background-size: cover;
			background-position: center;
		}

		.slider-item__wall-overlay
		{
			position: absolute;
			left: 0;
			top: 0;
			width: 36%;
			height: 100%;
			overflow: hidden;
			text-align: center;
			background-color: rgba(47, 56, 65, 0.7);
			z-index: 10;
			color: @white;
			opacity: 0;
			transition: all 300ms;

			*
			{
				display: block;
			}
		}

		.overlay__format-capton
		{
			margin-top: 1.1em;
			margin-bottom: 1em;
			// margin-bottom: 0.7em;
			font-size: 31/16em;
			text-transform: none;
		}

		.overlay__format
		{
			// font-size: 25/16em;
			font-size: 35/16em;
			padding-left: 16/35em;
			padding-right: 16/35em;
			.font_reg_cn();

			li
			{
				display: inline-block;
				margin-right: 0.2em;


				&:not(:last-child)
				{
					margin-bottom: 0.1em;
				}
			}			
		}

		.overlay__order
		{
			position: absolute;
			left: 50%;
			bottom: 0.9em;
			transform: translateX(-50%);
			font-size: 34/16em;
			.font_reg_cn();
		}
	// arrows
		.products__left-arrow,
		.products__right-arrow
		{
			top: 71%;
			transition: all 100ms;
			z-index: 10;
		}
		.products__left-arrow
		{
			position: absolute;
			left: 18%;
			// top: 57.5%;
			cursor: pointer;
			
			// short stick to slide
			&:after
			{
				// content: '';
				position: relative;
				left: 20.3/16em;
				top: -9/16em;
				display: block;
				width: 25/16em;
				height: 1px;
				background-color: @darkgrey;
				transform: rotate(-45deg);
			}
		}

		.products__right-arrow
		{
			position: absolute;
			right: 18%;
			// top: 57.5%;
			cursor: pointer;

			&:before
			{
				border-color: #ccc #ccc transparent transparent;

				@media screen and (min-width: 0px) and (max-width: 970px)
				{
					left: 6/16em;
					top: 7/16em;
				}

			}

			// short stick to slide
			&:after
			{
				// content: '';
				position: relative;
				left: -21/16em;
				top: 34/16em;
				display: block;
				width: 25/16em;
				height: 1px;
				background-color: @darkgrey;
				transform: rotate(-45deg);
			}
		}
		//   
		.products__stick
		{
			position: relative;
			top: -1.49em;
			display: block;
			width: 2px;
			height: 75/16em;
			.center();
			background-color: @darkgrey;
			opacity: 0.7;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: -2.5px;
				display: block;
				width: 7px;
				height: 7px;
				transform: rotate(45deg);
				background-color: @darkgrey;
			}
		}

// END PRODUCTS ===============================
