// GALLERY ====================================
	// common
		.gallery
		{
			padding-bottom: 130/16em;
			text-align: center;
			background-image: url("../assets/img/bg_lightgrey.jpg");
			background-size: cover;
		}

		.gallery__stick
		{
			.products__stick;
			top: 0/16em;
			height: 120/16em;
			margin-bottom: 25/16em;
		}

		.gallery__caption
		{
			.caption__default;
			margin-bottom: 45/39em;
		}
	// slider
		.gallery__slider
		{
			width: 98%;
			max-width: 1175/16em;
			.center();			
		}

		.gallery__slide
		{
			position: relative;
			display: table;
			width: 293/16em;
			height: 435/16em;
			overflow: hidden;
		}

		.gallery__img
		{
			position: absolute;
			width: 100%;
			height: 220/16em;
			background-size: cover;
			background-position: center;
		}

		.gallery__desc-wrap
		{
			position: absolute;
			width: 100%;
			height: 220/16em;
			transition: all 500ms;
		}

		// positioning
		.gallery__slide:nth-child(odd)
		{
			.gallery__img_wall
			{
				left: 0;
				top: 0;
			}

			.gallery__img_house
			{
				left: 0;
				bottom: -3/16em;
			}

			.gallery__desc-wrap
			{
				left: 0;
				bottom: 0;			
			}
		}

		.gallery__slide:nth-child(even)
		{
			.gallery__img_wall
			{
				left: 0;
				bottom: 0;
			}

			.gallery__img_house
			{
				left: 0;
				top: -3/16em;
			}

			.gallery__desc-wrap
			{
				right: 0;
				top: 0;
				width: 101%;
				right: -1%;
			}
		}

		// hover effect
		.gallery__slide:hover .gallery__desc-wrap
		{
			width: 0;
			font-size: 0;
		}

		// description
		.gallery__desc
		{
			position: relative;
			display: table-cell;
			width: 1%;
			vertical-align: middle;
			height: 220/16em;
			padding-left: 26/16em;
			padding-right: 26/16em;
			background-image: url("../assets/img/bg_feedback.jpg");
			background-size: cover;

			&:before
			{
				content: '';
				position: absolute;
				top: 0;
				left: 50%;
				width: 1px;
				height: 55/16em;
				background-color: @darkgrey;
			}

			span
			{
				display: block;
			}
		}

		.gallery__name
		{
			margin-bottom: 0.3em;
			font-size: 25/16em;
			@media screen and (max-width: 977px) { font-size: 18/16em; }
		}

		.gallery__about
		{
			text-transform: none;
			font-size: 20/16em;
			@media screen and (max-width: 977px) { font-size: 14/16em; }
		}
	// arrows
		.gallery__nav
		{
			position: relative;
			width: 222/16em;
			margin-top: 35/16em;
			.center();

			&:before
			{
				content: '';
				position: absolute;
				top: 12/16em;
				left: 30/16em;
				display: block;
				width: 165/16em;
				height: 1px;
				background-color: @darkgrey;
			}
		}

		.gallery__left-arrow
		{
			.left-arrow;
			position: absolute;
			left: 0;
		}

		.gallery__right-arrow
		{
			.right-arrow;
			position: absolute;
			right: 0;

			&:before
			{
				border-color: #ccc #ccc transparent transparent;
			}
		}
// END GALLERY ================================
