// COMMON STYLE ===============================
  html 
  {
    @media screen and (min-width: 1831px) { font-size: 135%; }
    @media screen and (min-width: 1751px) and (max-width: 1830px) { font-size: 130%; }
    @media screen and (min-width: 1691px) and (max-width: 1750px) { font-size: 125%; }
    @media screen and (min-width: 1621px) and (max-width: 1690px) { font-size: 120%; }
    @media screen and (min-width: 1531px) and (max-width: 1620px) { font-size: 115%; }
    @media screen and (min-width: 1461px) and (max-width: 1530px) { font-size: 110%; }
    @media screen and (min-width: 1401px) and (max-width: 1460px) { font-size: 105%; }
    @media screen and (min-width: 1301px) and (max-width: 1400px) { font-size: 100%; }
    @media screen and (min-width: 1232px) and (max-width: 1300px) { font-size: 95%; }
    @media screen and (min-width: 1181px) and (max-width: 1231px) { font-size: 90%; }
    @media screen and (min-width: 1106px) and (max-width: 1180px) { font-size: 85%; }
    @media screen and (min-width: 1042px) and (max-width: 1105px) { font-size: 80%; }
    @media screen and (min-width: 978px) and (max-width: 1041px) { font-size: 75%; }
    @media screen and (min-width: 915px) and (max-width: 977px) { font-size: 70%; }
    @media screen and (min-width: 852px) and (max-width: 914px) { font-size: 65%; }
    @media screen and (min-width: 788px) and (max-width: 851px) { font-size: 60%; }
    @media screen and (max-width: 787px) { font-size: 56%; }

  }

	body 
  {
		// display: table;
		min-height: 100vh;
		width: 100%;
		overflow-y: scroll;
    overflow-x: hidden;
		.font_light_cn();
		text-transform: uppercase;
    color: @darkgrey;
	}

	.main 
  {
		// display: table-row;
		height: 100%;
	}

	.container 
  {
		.center;
		.clearfix();
	}

  .caption__default
  {
    display: inline-block;
    padding: 0.2em 0.6em;
    border: 1px solid @darkgrey;
    font-size: 39/16em;
    .font_reg_cn();
  }

	a 
  {
		.link-hide-select();
	}

  // fixing the breaking overflow hidden in IE when sliding ("useCSS: false" in slider options is an alternative method, by animation starts lagging)
  .slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: none; -o-transform: translate3d(0, 0, 0); transform: none; }

  // dots and arrows
  	*
    {
  	  box-sizing: border-box;
  	}

  	.left-arrow,
  	.right-arrow {
  	  position: relative;
  	  display: inline-block;
  	  width: 26/16em;
  	  height: 26/16em;
  	  transform: rotate(45deg);
      cursor: pointer;
  	  &:before {
  	    content: '';
  	    position: absolute;
  	    border-style: solid;
  	    border-width: 7/16em 7/16em 7/16em 7/16em;
  	  }
  	}

  	.left-arrow {
  	  border: 1px solid @darkgrey;
  	  &:before {
  	    left: 5.5/16em;
  	    top: 5.5/16em;
        
        @media screen and (min-width: 1301px) and (max-width: 1400px)
        {
          left: 6/16em;
          top: 4/16em;
        }

        @media screen and (min-width: 978px) and (max-width: 1041px)
        {
          left: 6/16em;
          top: 4/16em;
        }

        @media screen and (min-width: 788px) and (max-width: 851px)
        {
          left: 6/16em;
          top: 4/16em;
        }


  	    border-color: transparent transparent @darkgrey @darkgrey;
  	  }
  	}

  	.right-arrow {
  	  background-color: @darkgrey;
  	  &:before {
        left: 6/16em;
        top: 6/16em;

        @media screen and (min-width: 1106px) and (max-width: 1180px)
        {
          left: 6/16em;
          top: 7/16em;
        }

        @media screen and (min-width: 852px) and (max-width: 977px)
        {
          left: 6/16em;
          top: 7/16em;
        }

        @media screen and (max-width: 787px)
        {
          left: 6/16em;
          top: 7/16em;
        }

  	    border-color: #fff #fff transparent transparent;
  	  }
  	}

  	.slider-dot
    {
  		> li
      {
        display: inline-block;
        width: 10/16em;
        height: 10/16em;
        border: 1px solid @darkgrey; 
        transform: rotate(45deg);
        background-color: #ccc;
        transition: all 200ms;

        button
        {
          display: none;
        }

        &:hover
        {
          background-color: @darkgrey;
        }

        &.slick-active
        {
          background-color: @darkgrey;
          border: 1px solid @darkgrey;
        }
      }
  	}
// END COMMON STYLE ===========================