// FOOTER =====================================
	.footer
	{
		position: relative;
		height: 165/16em;
		background-color: @darkgrey;
		color: @white;

		&:before
		{
			content: '';
			position: absolute;
			left: 70%;
			@media screen and (max-width: 977px) { left: 65%; }
			top: 0;
			display: block;
			width: 1px;
			height: 100/16em;
			background-color: #fff;
		}
	}

	.footer__wrap
	{
		.gallery__slider;
		.clearfix();
	}

	.footer__contacts
	{
		float: right;
		display: inline-block;
		margin-top: 3.5em;
		text-align: right;
	}

	.footer__address
	{
		font-size: 21/16em;
		@media screen and (max-width: 977px) { font-size: 14px; }
		display: block;
		text-transform: none;
		margin-bottom: 5px;
	}

	.footer__phone
	{
		.contacts__phone;
	}

// END FOOTER =================================
