// NETWORK ====================================
	// common styles
		.network
		{
			// padding-bottom: 25/16em;
			padding-bottom: 2%;
			text-align: center;
			background-image: url("../assets/img/bg_lightgrey.jpg");
			background-size: cover;
			color: @darkgrey;
		}

		.network__stick
		{
			.products__stick;
			top: 0/16em;
			height: 55/16em;
		}
	// header and states
		.network__caption
		{
			.caption__default;
			margin-bottom: 0.8em;
		}

		.network__states
		{
			margin-bottom: 0.6em;
		}

		.network__state
		{
			display: inline-block;
			margin-bottom: 0.5em;
			font-size: 30/16em;
			transition: all 100ms;

			&:not(:last-child)
			{
				margin-right: 41/30em;
			}
		}

		.network__state--active,
		.network__state:hover
		{
			// .font_reg_cn();
			text-shadow: 1px 1px 0px rgba(0,0,0,0.8);

		}
	// map
		.network__map
		{
			position: relative;
			z-index: 1;
		}

		.map__img
		{
			width: 100%;
		}

		.map__signature
		{
			position: absolute;
			.absolute_center();
			background-color: rgba(255, 255, 255, 0.8);
			padding: 12/16em;
			border: 1px solid @darkgrey;

			*
			{
				display: block;
				text-align: left;
				font-size: 16/16em;
				@media screen and (max-width: 1180px) { font-size: 14px; }
				&:not(:last-child)
				{
					margin-bottom: 4/16em;
				}
			}
		}

		.map__company
		{
			.font_reg_cn();
		}

		.map__address,
		.map__email,
		.map__storage
		{
			text-transform: none;
		}

		.map__address
		{
			padding-bottom: 23/16em;
		}
// END NETWORK ================================
