// ABOUT ======================================
	.about
	{
		background-color: @darkgrey;
		text-align: center;
		// .font_reg_cn();
		color: @white;
		padding: 2.7em 6.8em 7.2em 5.8em;
	}

	.about__caption
	{
		.caption__default;
		border: 1px solid @white;		
		margin-bottom: 1.65em;
	}

	.about__blocks
	{
		.clearfix();
	}

	.about__block
	{
		float: left;
		width: ~"calc(20% - 2.26em)";
		padding: 24/16em 33/16em;
		border: 1px solid @white;
		text-transform: none;
		text-align: left;

		&:not(:last-child)
		{
			margin-right: 2.8125em;			
		}
	}

	.about__toptext
	{
		position: relative;
		display: block;
		font-size: 70/16em;

		&:after
		{
			content: '';
			display: block;
			width: 100%;
			height: 1px;
			background-color: #bbbcc0;
		}
	}

	.about__unit
	{
		font-size: 40/70em;
		.font_light_cn();
	}

	.about__bottomtext
	{
		display: block;
		font-size: 40/16em;
	}
// END ABOUT ==================================
