// font-faces
	@font-face {
    font-family: 'akzidenz-grotesk_prolight';
    src: url('../assets/fonts/akzidgrtskprolig-webfont.woff2') format('woff2'),
         url('../assets/fonts/akzidgrtskprolig-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
	}

	@font-face {
    font-family: 'akzidenz-grotesk_proRgCn';
    src: url('../assets/fonts/akzidgrtskprocnd-webfont.woff2') format('woff2'),
         url('../assets/fonts/akzidgrtskprocnd-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
	}

	@font-face {
    font-family: 'akzidenz-grotesk_proBdCn';
    src: url('../assets/fonts/akzidgrtskprobolcnd-webfont.woff2') format('woff2'),
         url('../assets/fonts/akzidgrtskprobolcnd-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
	}

	@font-face {
    font-family: 'akzidenz-grotesk_proLtCn';
