         url('../assets/fonts/akzidgrtskproligcnd-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
	}	
// mixins

	
	.font_reg_cn
	{
		font-family: 'akzidenz-grotesk_proRgCn', sans-serif;
